.contact-cards {
    padding-top: 1.5rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.contact-card {
    width: 7rem;
    /* height: 128px; */
    padding: 1rem;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    background-color: var(--color-bg);
    transition: var(--transition);
}

.contact-card-icon {
    font-size: 2rem;
    text-align: center;
}

.contact-description {
    padding-top: 1.5rem;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.contact-preferred {
    display: flex;
    gap: 0.5rem;
}

.contact-texts {
    padding-top: 1.5rem;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.contact-text {
    display: flex;
    /* margin: auto; */
    gap: 0.5rem;
    /* width: 100%; */
}

.contact-text h3 {
    flex-grow: 0;
}

.contact-text p {
    flex-grow: 1;
}